"use client";

import React, { useState, useRef, useEffect } from 'react';
import { ChevronDown, Search } from 'lucide-react';

const SearchAndSelect = () => {
  const [dropdown, setDropdown] = useState(false);
  const [selectedOption, setSelectedOption] = useState('Select');
  const [searchTerm, setSearchTerm] = useState('');
  const dropdownRef = useRef<HTMLDivElement>(null);

  // Close dropdown when clicking outside
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target as Node)) {
        setDropdown(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);

  const handleSelect = (option: string) => {
    setSelectedOption(option);
    setDropdown(false);
  };

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (selectedOption !== 'Select') {
      console.log('Submitted:', selectedOption, 'Search term:', searchTerm);
      // Add your submission logic here
    }
  };

  const options = [
    { id: 1, label: 'Nutritional Info' },
    { id: 2, label: 'Recipes' },
    { id: 3, label: 'Workout Plans' },
    { id: 4, label: 'Supplements' }
  ];

  return (
    <form onSubmit={handleSubmit} className="flex items-center gap-2 w-full max-w-xs">
      <div className="relative flex-1 min-w-0">
        <div className="absolute inset-y-0 left-0 flex items-center pl-3 pointer-events-none">
          <Search className="w-4 h-4 text-gray-400" />
        </div>
        <input
          type="search"
          className="w-full pl-10 pr-3 py-2 text-sm text-gray-200 bg-gray-800/50 border border-gray-700 
                   rounded-full focus:ring-2 focus:ring-cyan-500/60 focus:border-cyan-500/60 focus:outline-none
                   placeholder:text-gray-500"
          placeholder="Search info..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          required
        />
      </div>
      
      <div className="relative" ref={dropdownRef}>
        <button
          type="button"
          className="flex items-center px-4 py-2 text-sm font-medium text-gray-200 bg-gradient-to-r 
                   from-cyan-600 to-cyan-800 rounded-full hover:from-cyan-500 hover:to-cyan-700 
                   focus:ring-2 focus:ring-cyan-500 focus:outline-none transition-all duration-200 
                   shadow-lg shadow-cyan-900/20"
          onClick={() => setDropdown(!dropdown)}
        >
          <span className="truncate max-w-24">{selectedOption}</span>
          <ChevronDown 
            className={`w-4 h-4 ml-2 transition-transform duration-300 ${dropdown ? 'rotate-180' : ''}`}
          />
        </button>
        
        {dropdown && (
          <div className="absolute right-0 mt-2 w-48 bg-gray-800/90 backdrop-blur-lg border border-gray-700/50 
                        rounded-xl shadow-xl z-50 overflow-hidden transform origin-top scale-100 animate-in fade-in duration-200">
            <ul className="py-1">
              {options.map((option) => (
                <li key={option.id}>
                  <button
                    type="button"
                    onClick={() => handleSelect(option.label)}
                    className="w-full text-left px-4 py-2 text-sm text-gray-200 hover:bg-gradient-to-r 
                             hover:from-cyan-600/40 hover:to-cyan-800/40 transition-colors duration-150"
                  >
                    {option.label}
                  </button>
                </li>
              ))}
            </ul>
          </div>
        )}
      </div>
      
      <button
        type="submit"
        className="hidden sm:flex items-center px-4 py-2 text-sm font-medium text-gray-200 bg-gradient-to-r 
                 from-cyan-600 to-cyan-800 rounded-full hover:from-cyan-500 hover:to-cyan-700 
                 focus:ring-2 focus:ring-cyan-500 focus:outline-none transition-all duration-200 
                 shadow-lg shadow-cyan-900/20"
      >
        Search
      </button>
    </form>
  );
};

export default SearchAndSelect;